	export default {
		data() {
			return {
            details:[]
			}
		},
		onLoad(option) {
			
		    var that = this;
			
			uni.request({
				url: 'http://106.13.239.202/index.php?c=rent&m=checkdetails',
				method: 'POST',
				header: {"content-type": "application/x-www-form-urlencoded"}, 
				data: {
					
					phone:option.phone
					
				},
				success: res => {
					
				 console.log(res.data.data)
				   that.details = res.data.data;
				},
				fail: () => {
				
				},
				complete: () => {}
			});
			
			
		},
		methods: {

		}
	}
